
body {
  margin: 0;
  padding: 0;
  font-family: 'Rubik', sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

.App {
  text-align: center;
  position: relative;
  min-height: 100vh;
}

.main-content {
  padding-bottom: 2.5rem;
}

.App-logo {
  height: 25vmin;
  pointer-events: none;
}

.App-header {
  margin-top: 50px;
  min-height: 10vh;
  display: -webkit-flex;
  display: flex;
  -webkit-align-items: flex-start;
          align-items: flex-start;
  -webkit-justify-content: center;
          justify-content: center;
}

.counters {
  margin-top: 40px;
  display: -webkit-flex;
  display: flex;
  -webkit-justify-content: center;
          justify-content: center;
}

.counter-forms {
  margin-bottom: 50px;
  color: #555;
}

.footer {
  background-color: #555;
  color: white;
  font-size: 14px;
  line-height: 2.5rem;
  position: absolute;
  bottom: 0;
  width: 100%;
  height: 2.5rem;
}
.new-btn {
    border: none;
    outline: none; 
    display: inline-block;
    text-decoration: none;
    background: #ddd;
    color: white;
    width: 50px;
    height: 50px;
    line-height: 50px;
    border-radius: 50%;
    text-align: center;
    vertical-align: middle;
    overflow: hidden;
    transition: .4s;
}

.new-btn:hover {
    background: #888;
}
.counter {
    border: thin solid #eee;
    padding: 50px;
    display: -webkit-flex;
    display: flex;
    -webkit-justify-content: center;
            justify-content: center;
    position: relative;
    color: #555;
    border-radius: 15px;
    margin-bottom: 20px;
}

input, span, label {
    display: block;
    margin: 10px;
    padding: 5px;
    border: none;
    resize: none;
}

input:focus {
    outline: 0;
}

input.question {
    color: #555;
    font-size: 16px;
    font-weight: lighter;
    border-radius: 2px;
    margin: 0;
    border: none;
    width: 100%;
    background: rgba(0, 0, 0, 0);
    transition: padding-top 0.2s ease, margin-top 0.2s ease;
    overflow-x: hidden;
    /* Hack to make "rows" attribute apply in Firefox. */
}

input.question+label {
    display: block;
    position: relative;
    white-space: nowrap;
    padding: 0;
    margin: 0;
    padding-top: 10px;
    width: 10%;
    border-top: 1px solid lightseagreen;
    transition: width 0.4s ease;
    height: 0px;
}

input.question:focus+label {
    width: 100%;
}

input.question:focus, input.question:valid {
    padding-top: 40px;
}

input.question:focus+label>span, input.question:valid+label>span {
    top: -50px;
    font-size: 14px;
    color: #ccc;
}

input.question+label>span {
    text-transform: uppercase;
    margin: 0;
    position: absolute;
    color: #888;
    font-size: 16px;
    top: -30px;
    left: 0px;
    z-index: -1;
    transition: top 0.2s ease, font-size 0.2s ease, color 0.2s ease;
}

.info {
    /* background-color: lightgreen; */
    display: -webkit-flex;
    display: flex;
    -webkit-flex-direction: column;
            flex-direction: column;
    -webkit-justify-content: center;
            justify-content: center;
    -webkit-align-items: flex-start;
            align-items: flex-start;
    /* padding-right: 40px; */
    padding-left: 20px;
}

.info .label {
    text-transform: uppercase;
    color: #888;
    font-size: 16px;
    margin-bottom: 10px;
}

.info .value {
    margin-bottom: 30px;
}

.controls {
    /* background-color: lightblue; */
    display: -webkit-flex;
    display: flex;
    -webkit-flex-direction: column;
            flex-direction: column;
    -webkit-justify-content: center;
            justify-content: center;
    -webkit-align-items: center;
            align-items: center;
    padding-left: 100px;
    padding-right: 100px;
}

.controls .current-count {
    font-size: 54px;
    font-weight: lighter;
    width:65px;
}

.controls .arrow-control {
    font-size: 44px;
    padding-top: 10px;
    padding-bottom: 10px;
    cursor: pointer;
}

.controls .arrow-control:hover {
    color: lightseagreen;
}

.removeBtn {
    position: absolute;
    top: 12px;
    right: 12px;
    cursor: pointer;
    color: #ddd;
}

.removeBtn:hover {
    color: #555;
}

.counters-list {
    display:-webkit-flex;
    display:flex;
    -webkit-flex-direction: column;
            flex-direction: column;
}
.empty-list-banner {
    color: #555;
    margin-bottom: 50px;
}
